import React from 'react';
import './TokenInfo.scss';

import classNames from 'classnames';
import { fade, withStyles, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Paper from '@material-ui/core/Paper';
import Box from '@material-ui/core/Box';
import Toolbar from '@material-ui/core/Toolbar';
import InputBase from '@material-ui/core/InputBase';
import SearchIcon from '@material-ui/icons/Search';
import TextField from '@material-ui/core/TextField';
import AccountCircle from '@material-ui/icons/AccountCircle';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Container from '@material-ui/core/Container';
import LinearProgress from '@material-ui/core/LinearProgress';

import Typography from '@material-ui/core/Typography';
import BigNumber from 'bignumber.js';
import commaNumber from 'comma-number';
import { withConsumer } from '../../Context';

import {
  findTokens,
  findMarketMetrics,
  getScotConfig,
  findAllTokenBalance,
} from '../../service/SteemEngineService';

const useStyles = makeStyles(theme => ({
  box: {
    // margin: theme.spacing(1),
    padding: theme.spacing(2),
    height: 'auto',
    display: 'block',
    marginBottom: 0,
  },
}));

const TokenInfo = ({ location, history, match, context }) => {
  console.log('TokenInfo', { location, history, match });
  const classes = useStyles();

  const [loading, setLoading] = React.useState(true);
  const [token, setToken] = React.useState();
  const [market, setMarket] = React.useState();
  const [holders, setHolders] = React.useState([]);
  const [orderBy, setOrderBy] = React.useState('balanceStakeSum');
  const [sortOrder, setSortOrder] = React.useState('desc');

  const sortedHolders = React.useMemo(() => {
    return holders.sort((a, b) => {
      if (sortOrder === 'desc') return b[orderBy] - a[orderBy];
      return a[orderBy] - b[orderBy];
    });
    // .sort((a, b) => b[orderBy] - a[orderBy]);
  }, [holders, orderBy, sortOrder]);

  React.useEffect(() => {
    setLoading(true);
    setHolders([]);

    const { symbol } = match.params;

    Promise.all([
      findTokens({ query: { symbol } }),
      findMarketMetrics(symbol),
    ]).then(([[_token], [_market]]) => {
      console.log({ _token, _market });
      const metadata = JSON.parse(_token.metadata);
      setToken({
        ..._token,
        ...metadata,
      });
      setMarket(_market);
    });

    findAllTokenBalance(symbol).then(result => {
      console.log('findAllTokenBalance', result);
      const holders = result
        .map(holder => {
          const balance = parseFloat(holder.balance || 0);
          const stake = parseFloat(holder.stake || 0);
          const delegationsIn = parseFloat(
            holder.delegationsIn || holder.receivedStake || 0,
          );
          const delegationsOut = parseFloat(
            holder.delegationsOut || holder.delegatedStake || 0,
          );
          const pendingUndelegations = parseFloat(
            holder.pendingUndelegations || 0,
          );
          const pendingUnstake = parseFloat(holder.pendingUnstake || 0);
          const balanceStakeSum = balance + stake + delegationsOut;
          const total =
            balance +
            stake +
            pendingUnstake +
            pendingUndelegations +
            delegationsOut;
          return {
            account: holder.account,
            balance,
            // delegatedStake: parseFloat(holder.delegatedStake || 0),
            pendingUnstake,
            pendingUndelegations,
            // receivedStake: parseFloat(holder.receivedStake || 0),
            stake,
            balanceStakeSum,
            delegationsIn,
            delegationsOut,
            total,
          };
        })
        .filter(({ account, total }) => account !== 'null' && total > 0);
      setHolders(holders);
      setLoading(false);
    });
  }, []);

  const holderCount = React.useMemo(() => {
    return holders.length;
  }, [holders]);

  const toogleSortHandeler = val => {
    if (val === orderBy) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setOrderBy(val);
      setSortOrder('desc');
    }
  };

  return (
    <Container maxWidth="lg" className={classes.container}>
      <Grid container spacing={3}>
        <Grid item xs={12}>
          <Paper className={classes.root}>
            {/* <div>TokenInfo</div> */}

            <div className="ivu-card-body">
              {token && (
                <React.Fragment>
                  <div className="ivu-row">
                    <div className={classNames('ivu-col', 'ivu-col-span-18')}>
                      {token.icon.startsWith('http') && (
                        <img className="token-logo" src={token.icon} alt="" />
                      )}
                      <div className="token-name">{token.symbol}</div>
                      {/* <div class="token-scot-setting" style="display: none;">
                                Cashout Window Days: <strong >0</strong> Day
                            </div>  */}
                      <div className="token-detail">{token.name}</div>
                      <div className="token-detail">{token.desc}</div>
                      <div className="token-detail">
                        {token.url && (
                          <a
                            href={token.url}
                            target="_blank"
                            rel="noopener noreferrer"
                            className="steem-keychain-checked"
                          >
                            {token.url}
                          </a>
                        )}
                      </div>
                      <div className="token-detail">
                        <span>
                          <span style={{ color: '#84929c' }}>issuer:</span>
                          <a
                            href={`https://steemit.com/@${token.issuer}`}
                            className="steem-keychain-checked"
                            target="_blank"
                            rel="noopener noreferrer"
                          >
                            @{token.issuer}
                          </a>
                        </span>
                      </div>
                    </div>
                  </div>
                  <div
                    className={classNames(
                      'ivu-divider',
                      'ivu-divider-horizontal',
                      'ivu-divider-default',
                    )}
                  />
                  <div className="ivu-row" style={{ paddingLeft: 20 }}>
                    {market && (
                      <div className={classNames('ivu-col', 'ivu-col-span-7')}>
                        <div className={classNames('token-price-steem')}>
                          <strong>{market.lastPrice}</strong>{' '}
                          <span className="steem-sign">STEEM</span>
                        </div>
                        <div className="token-price-dollar">
                           $
                          {new BigNumber(market.lastPrice)
                            .times(context.steemPrice)
                            .toFixed(3)}
                        </div>
                        <div className="token-price-detail">
                          {parseFloat(market.priceChangePercent)}%{' '}
                          {parseFloat(market.priceChangeSteem)}
                        </div>
                      </div>
                    )}
                    <div className={classNames('ivu-col', 'ivu-col-span-11')}>
                      <div className="token-supply">
                         Circulating Supply
                        {commaNumber(
                          parseFloat(token.circulatingSupply).toFixed(0),
                        )}{' '}
                        {token.symbol}
                      </div>
                      <div className="token-supply">
                         Max Supply
                        {commaNumber(
                          parseFloat(token.maxSupply).toFixed(0),
                        )}{' '}
                        {token.symbol}
                      </div>
                      <div className="token-supply">
                         Total Staked
                        {commaNumber(token.totalStaked)} {token.symbol}
                      </div>
                    </div>
                  </div>
                </React.Fragment>
              )}
            </div>
            {token && market ? null : <LinearProgress />}
          </Paper>
        </Grid>
        <Grid item xs={12}>
          <Typography variant="h5">Holder</Typography>
          <Paper className={classes.root}>
            <Box component="span" m={2} className={classes.box}>
              {market && (
                <React.Fragment>
                  <div className="ivu-row">
                    <span
                      className={classNames('ivu-col', 'tit', 'ivu-col-span-4')}
                    >
                      24H Volume
                    </span>
                    <span
                      className={classNames('ivu-col', 'val', 'ivu-col-span-8')}
                    >
                      {market.volume} STEEM
                    </span>
                  </div>
                </React.Fragment>
              )}
              <div className="ivu-row">
                <span
                  className={classNames('ivu-col', 'tit', 'ivu-col-span-4')}
                >
                  Accounts
                </span>
                <span
                  className={classNames('ivu-col', 'val', 'ivu-col-span-8')}
                >
                  {holderCount}
                </span>
                {/* <span className={classNames('ivu-col', 'ivu-col-span-12')} /> */}
              </div>
            </Box>
            <Divider />
            {loading ? <LinearProgress /> : null}
            <Table className={classes.table} size="small">
              <TableHead>
                <TableRow>
                  <TableCell>No.</TableCell>
                  <TableCell>Account</TableCell>
                  <TableCell align="right">
                    <TableSortLabel
                      active={orderBy === 'balanceStakeSum'}
                      direction={sortOrder}
                      //   direction={order}
                      onClick={() => toogleSortHandeler('balanceStakeSum')}
                    >
                      Stake+Balance
                    </TableSortLabel>
                  </TableCell>
                  <TableCell align="right" disablePadding="none">
                    <TableSortLabel
                      active={orderBy === 'stake'}
                      direction={sortOrder}
                      onClick={() => toogleSortHandeler('stake')}
                    >
                      Stake
                    </TableSortLabel>
                  </TableCell>
                  <TableCell align="right">
                    <TableSortLabel
                      active={orderBy === 'balance'}
                      direction={sortOrder}
                      onClick={() => toogleSortHandeler('balance')}
                    >
                      Balance
                    </TableSortLabel>
                  </TableCell>
                  <TableCell align="right">
                    <TableSortLabel
                      active={orderBy === 'pendingUnstake'}
                      direction={sortOrder}
                      onClick={() => toogleSortHandeler('pendingUnstake')}
                    >
                      Pending Unstaking
                    </TableSortLabel>
                  </TableCell>
                  {/* <TableCell align="right">Selling</TableCell> */}
                  {/* <TableCell align="right">Unstaking</TableCell> */}
                  {/* <TableCell align="right">St+Ba</TableCell> */}
                  {/* <TableCell align="right">VotingPower</TableCell> */}
                  <TableCell align="right" numeric>
                    <TableSortLabel
                      active={orderBy === 'delegationsOut'}
                      direction={sortOrder}
                      onClick={() => toogleSortHandeler('delegationsOut')}
                    >
                      Delegated
                    </TableSortLabel>
                  </TableCell>
                  <TableCell align="right" numeric>
                    <TableSortLabel
                      active={orderBy === 'delegationsIn'}
                      direction={sortOrder}
                      onClick={() => toogleSortHandeler('delegationsIn')}
                    >
                      Received
                    </TableSortLabel>
                  </TableCell>
                  {/* <TableCell align="right">Price</TableCell> */}
                </TableRow>
              </TableHead>
              <TableBody>
                {sortedHolders.map((holder, index) => (
                  <TableRow key={holder.account}>
                    <TableCell component="th" scope="row">
                      {index + 1}
                    </TableCell>
                    <TableCell component="th" scope="row">
                      {holder.account}
                    </TableCell>
                    <TableCell align="right">
                      {holder.balanceStakeSum &&
                        commaNumber(holder.balanceStakeSum.toFixed(3))}
                    </TableCell>
                    <TableCell align="right" disablePadding="none">
                      {holder.stake && commaNumber(holder.stake.toFixed(3))}
                    </TableCell>
                    <TableCell align="right">
                      {holder.balance && commaNumber(holder.balance.toFixed(3))}
                    </TableCell>
                    <TableCell align="right">
                      {holder.pendingUnstake &&
                        commaNumber(holder.pendingUnstake.toFixed(3))}
                    </TableCell>
                    <TableCell align="right">
                      {holder.delegationsOut &&
                        commaNumber(holder.delegationsOut.toFixed(3))}
                    </TableCell>
                    <TableCell align="right">
                      {holder.delegationsIn &&
                        commaNumber(holder.delegationsIn.toFixed(3))}
                    </TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </Paper>
        </Grid>
      </Grid>
    </Container>
  );
};

export default withConsumer(TokenInfo);
